import React, { useState } from 'react';


function ImageHoverMask({ index, src, title, content }) {
    const [hover, setHover] = useState(false);
    const toggleHover = () => {
        setHover(!hover);  // 切换 hover 状态
    };


    return (
        <div className="mb-4 relative" onMouseEnter={() => setHover(true)}
            onMouseLeave={() => setHover(false)}
            onClick={toggleHover}>
            <img src={process.env.PUBLIC_URL + `${src}`} className="w-full h-auto rounded-lg shadow-md border border-4 border-white " alt="" />
            <div className="absolute inset-0 bg-black bg-opacity-20 rounded-lg border border-4 border-white "></div>
            {hover && (
                <div className="absolute inset-0 bg-black bg-opacity-50 rounded-lg border border-4 border-white ">
                    <div className='absolute bottom-0 text-wt p-5'>
                        <div className='text-2xl font-medium mb-2'>{title}</div>
                        <div className='text-lg'>{content}</div>
                    </div>
                </div>
            )}
        </div>
    )
}

export default ImageHoverMask