import React from 'react';
import ImageHoverMask from '../components/Hover/ImageHoverMask';


function Works(params) {


    const images = [
        { id: 1, src: process.env.PUBLIC_URL + `/image/1.jpg`, title: "Design Admin System Dashboardet", content: "Participated in the UI design and programmed of dashboard front-end." },
        { id: 2, src: process.env.PUBLIC_URL + `/image/2.jpg`, title: "Product Details", content: "Implemented product carousels，product gallery，and zooming functions. Optimize a website's performance and page load speed" },
        { id: 3, src: process.env.PUBLIC_URL + `/image/4.jpg`, title: "Redesign Project", content: "Collaborated with a team to redesign the website." },
        { id: 4, src: process.env.PUBLIC_URL + `/image/3.jpg`, title: "Static Website", content: "Static Website" },



        // 更多图片路径
    ];

    return (
        <section id="works" className="works bg-bkll text-wt pt-10 md:pt-48">
            <div className='title text-center pt-16'>
                <h1 className='font-medium mb-5 md:text-3xl'>Works</h1>
                <p className='text-gy'>Here are some web projects displayed from my past works. </p>
                <p className='text-gy'>If you'd like to know more, please contact me.</p>
            </div>

            <div className="grid grid-cols-1 gap-5 -mt-16 scale-75 md:grid-cols-2">
                {images.map((image) => (
                    <div key={image.id}> 
                    <ImageHoverMask
                        index={image.id}
                        src={image.src}
                        title={image.title}
                        content={image.content}
                    />
                    </div>
                ))}
                {/* key要上给最外层的元素，如果有问题，实在不行自己加一层 */}
            </div>
        </section>
    )
}
export default Works